            // í•´ë‹¹í•˜ì§€ ì•ŠëŠ” ê°’ì€ nullì¸ì¤„ ì•Œì•˜ìœ¼ë‚˜ ì½˜ì†”ì°ì–´ë³´ë‹ˆ ë¹ˆë°°ì—´ì´ì–´ì„œ .lengthë¡œ ë¹ˆë°°ì—´íŒë‹¨
